.aboutMe {
    display: flex;

}

.aboutMe-image {
    /* background-image: url('../../assets/possibility.png');
    background-color: var(--color-bg);
    background-blend-mode: normal;
    background-size: 70%;
    background-repeat: no-repeat; */
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 5rem;

}

.aboutMe-image img {
    width: 80%;
    height: 80%;
    margin: 5rem;

}

.aboutMe-content{

    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding: 5%;
}

.aboutMe-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 40px;
}

.aboutMe-content p{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 20px;
    color: var(--color-text);
    margin-top: 2rem;

}

