.gpt3__header {
    display: flex;

}

.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.gpt3__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 70px;
    line-height: 75px;
    letter-spacing: -0.04px;
}

.gpt3__header-content p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: var(--color-text);

    margin-top: 1.5rem;
}

.gpt3__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px){
    .gpt3__header{
        flex-direction: column;
    }
}

@media screen and (max-width: 650px){
    .gpt3__header h1{
        font-size: 44px;
        line-height: 50px;
    }
    .gpt3__header p{
        font-size: 15px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px){
    .gpt3__header h1{
        font-size: 36px;
        line-height: 35px;
    }
    .gpt3__header p{
        font-size: 13px;
        line-height: 24px;
    }
}

