.gpt3__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.gp3__features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    padding: 0 0 2rem;
}

.gp3__features-heading h1 {
    padding: 20px;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 40px;
}

.gpt3__features-container{
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
