root{--border-color: #fff;}

.gpt3__brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.gpt3__brand div{
    flex: 1;
    max-width: 120px;
    min-width: 120px;
    margin: 1rem;

    display: flex;
    justify-content: center;
    align-items: center;

}

.linkedin, .github, .mail, .instagram{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 5rem;
    box-sizing: border-box;
}

  .linkedin img, .github img, .mail img, .instagram img{
    width: 140%;
    height: 140%;
    cursor: pointer;
    padding: 5px;
}

